import React from "react"
import {Switch,Route} from "react-router-dom"

//import components needed

import Home from "./Components/Home/Home.js"
import Settings from "./Components/Settings/Settings.js"
import Login from "./Components/Login/Login.js"
import AddStaff from "./Components/AddStaff/AddStaff.js"
import ManageAdmin from "./Components/ManageAdmin/ManageAdmin.js"
import ManageStaff from "./Components/ManageStaff/ManageStaff.js"
import ManageCompany from "./Components/ManageCompany/ManageCompany.js"
import AddVendor from "./Components/AddVendor/AddVendor.js"
import AddCustomer from "./Components/AddCustomer/AddCustomer.js"
import ManageVendor from "./Components/ManageVendor/ManageVendor.js"
import ManageCustomer from "./Components/ManageCustomer/ManageCustomer.js"
import AddProduct from "./Components/AddProduct/AddProduct.js"
import ManageProduct from "./Components/ManageProduct/ManageProduct.js"
import AddQuantity from "./Components/AddQuantity/AddQuantity.js"
import ManageQuantity from "./Components/ManageQuantity/ManageQuantity.js"
import Sales from "./Components/Sales/Sales.js"
import Payment from "./Components/Payment/Payment.js"
import SalesReport from "./Components/SalesReport/SalesReport.js"
import PaymentReport from "./Components/PaymentReport/PaymentReport.js"
import AddDriver from "./Components/AddDriver/AddDriver.js"
import AddVehicle from "./Components/AddVehicle/AddVehicle.js"
//import Mapping from "./Components/Mapping/Mapping.js"
import Supply from "./Components/Supply/Supply.js"
import ManageDriver from "./Components/ManageDriver/ManageDriver.js"
import ManageVehicle from "./Components/ManageVehicle/ManageVehicle.js"
//import ManageMapping from "./Components/ManageMapping/ManageMapping.js"
import SupplyReport from "./Components/SupplyReport/SupplyReport.js"

//creating a functional component for routing

const Main = ()=>{

  return(
    <Switch>

      <Route exact path = "/" component = {Home} />
      <Route path = "/home" component = {Home} />
      <Route path
       = "/index" component = {Home} />
      <Route path = "/settings" component = {Settings} />
      <Route path = "/login" component = {Login} />
      <Route exact path = "/dashboard/" component = {AddStaff} />
      <Route exact path = "/dashboard/admin/" component = {AddStaff} />
      <Route exact path = "/dashboard/staff/" component = {AddCustomer} />
      <Route path = "/dashboard/admin/adduser" component = {AddStaff} />
      <Route path = "/dashboard/admin/manageadmin" component = {ManageAdmin} />
      <Route path = "/dashboard/admin/managestaff" component = {ManageStaff} />
      <Route path = "/dashboard/admin/managecompany" component = {ManageCompany} />
      <Route path = "/dashboard/staff/addvendor" component = {AddVendor} />
      <Route path = "/dashboard/staff/addcustomer" component = {AddCustomer} />
      <Route path = "/dashboard/staff/managevendor" component = {ManageVendor} />
      <Route path = "/dashboard/staff/managecustomer" component = {ManageCustomer} />
      <Route path = "/dashboard/staff/addproduct" component = {AddProduct} />
      <Route path = "/dashboard/staff/manageproduct" component = {ManageProduct} />
      <Route path = "/dashboard/staff/addquantity" component = {AddQuantity} />
      <Route path = "/dashboard/staff/managequantity" component = {ManageQuantity} />
      <Route exact path = "/dashboard/inventory/" component = {Sales} />
      <Route path = "/dashboard/inventory/sales" component = {Sales} />
      <Route path = "/dashboard/inventory/payment" component = {Payment} />
      <Route path = "/dashboard/inventory/managesales" component = {SalesReport} />
      <Route path = "/dashboard/inventory/managepayment" component = {PaymentReport} />
      <Route exact path = "/dashboard/truck/" component = {AddDriver} />
      <Route path = "/dashboard/truck/adddriver" component = {AddDriver} />
      <Route path = "/dashboard/truck/addvehicle" component = {AddVehicle} />
     
      <Route path = "/dashboard/truck/supply" component = {Supply} />
      <Route path = "/dashboard/truck/managedriver" component = {ManageDriver} />
      <Route path = "/dashboard/truck/managevehicle" component = {ManageVehicle} />
     
      <Route path = "/dashboard/truck/managesupply" component = {SupplyReport} />
    </Switch>
  )
}


export default Main
